/**
 * Custom styling for the "I don't" page
 *
 * @version 1.0.0
 * @package cjbarnes.github.io
 */
.donts {
  /* Rows of Dos/Don'ts. */
  /**
     * Dos/Don'ts figure elements.
     */
  /* Dos box and icon. */
  /* Don'ts box and icon. */
  /* Both Dos and Don'ts. Placed here so the padding overrides `column()`. */ }
  .donts .donts-row:before, .donts .donts-row:after {
    content: " ";
    display: table; }
  .donts .donts-row:after {
    clear: both; }
  .donts figure.yes {
    float: left;
    display: inline;
    width: 50%;
    padding: 0 15px;
    margin-left: 8.33333%;
    /* Icon. */
    /* The quote itself. Smaller text, as there's more of it. */ }
    .donts figure.yes:before {
      content: "\e65c";
      background-color: #2b9759; }
    .donts figure.yes blockquote {
      font-size: 18px;
      font-size: 1rem;
      line-height: 1.66667;
      color: #2b9759; }
      .donts figure.yes blockquote p + p {
        margin-top: 1.66667rem; }
  .donts figure.no {
    float: left;
    display: inline;
    width: 33.33333%;
    padding: 0 15px;
    margin-left: 8.33333%;
    /* Icon. */
    /* The quote itself. */ }
    .donts figure.no:before {
      content: "\e65b";
      background-color: #8f2823; }
    .donts figure.no blockquote {
      font-style: italic;
      color: #8f2823; }
  .donts figure.yes, .donts figure.no {
    position: relative;
    margin-top: 60px;
    margin-top: 3.33333rem;
    margin-bottom: 30px;
    margin-bottom: 1.66667rem;
    padding-top: 45px;
    padding-top: 2.5rem;
    padding-bottom: 45px;
    padding-bottom: 2.5rem;
    background-color: #f5f5f5;
    /*
         * 1. Match the background the figures sit on.
         */ }
    .donts figure.yes:before, .donts figure.no:before {
      display: block;
      overflow: hidden;
      width: 49px;
      height: 49px;
      border: 2px solid;
      border-radius: 50%;
      line-height: 45px;
      text-align: center;
      font-size: 28px;
      font-size: 1.55556rem;
      position: absolute;
      top: -24.5px;
      left: 50%;
      margin-left: -24.5px;
      border-color: #f5f5f5;
      color: #f5f5f5; }
    .donts figure.yes:before, .donts figure.no:before {
      font-family: "cj-icons";
      border-color: #e0d7cb;
      /* 1 */ }

